.song{
    min-height: 60vh;
    
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    img{
        width: 20%;
        border-radius: 50%;
    }
    h2{
        padding: 3rem 1rem 1rem 1rem;
    }
    h3{
        font-size: 1rem;
    }
}
@media screen and(max-width : 670px) {
    .song{
        img {
            width: 60%;
        }
    }
    
}