.player{
    min-height: 20vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.time-control{
    display: flex;
    width: 50%;
    align-items: center;
    input{
        width: 100%;
        -webkit-appearance: none;
        background: transparent;
        cursor: pointer;
    }
    p{
        padding: 1rem;
    }
}

.play-control{
    display: flex;
    width: 30%;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    svg{
        cursor: pointer;
    }
}

input[type="range"]:focus{
    outline: none;
}

input[type="range"]::-webkit-slider-thumb{
    -webkit-appearance: none;
    width: 16px;
    height: 16px;
    
}
input[type="range"]::-moz-range-thumb{
    -webkit-appearance: none;
    background: none;
    border: none;
    
}

.track{
    background: chartreuse;
    width: 100%;
    height: 1rem;
    position: relative;
    border-radius: 1rem;
    overflow: hidden;
}


.animate-track {
    background:gray;
    width: 100%;
    height: 100%;
    position: absolute;
    top:0;
    left:0;
    transform: translateX(0%);
    pointer-events: none;

}

@media screen and (max-width:670px) {
    .time-control{
        width: 90%;
    }
    .play-control{
        width: 60%;
    }
}